/******************
     BODY TEXT
*******************/
.body--default, body,
p,
ul,
ol {
  color: #000;
  font-family: "Montserrat", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 19px;
  line-height: 26px; }

/* Classes for Body Text */
em,
i {
  font-style: italic; }

strong {
  font-weight: 600; }

p:not(:last-child) {
  margin-bottom: 30px;
  text-align: center; }

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative; }

body {
  background-color: #fff;
  color: #000;
  margin: 0; }

img {
  max-width: 100%; }

a,
button,
input {
  outline: none; }

a {
  color: inherit;
  cursor: pointer; }

.row {
  margin-left: -15px;
  margin-right: -15px;
  width: calc(100% + 30px); }

.container {
  margin: 0 auto;
  max-width: 1200px;
  position: relative; }
  .container::after {
    clear: both;
    content: "";
    display: block; }

section {
  margin-bottom: 80px;
  position: relative;
  width: 100%; }
  section::after {
    clear: both;
    content: "";
    display: block; }
  @media only screen and (min-width: 768px) {
    section {
      margin-bottom: 96px; } }
  @media only screen and (min-width: 992px) {
    section {
      margin-bottom: 120px; } }

/********************
     Transitions
*********************/
a {
  -ms-transition: all 0.5s cubic-bezier(0, 0, 0.2, 1);
  -webkit-transition: all 0.5s cubic-bezier(0, 0, 0.2, 1);
  transition: all 0.5s cubic-bezier(0, 0, 0.2, 1); }

/********************
      HERO
********************/
header {
  padding-top: 5px;
  text-align: center; }

.user-wrapper {
  height: calc(100vh - 104px);
  position: relative;
  width: 100%; }
  .user-wrapper:before {
    background: url("../../assets/images/2.png") center center no-repeat;
    background-size: cover;
    content: '';
    height: 100%;
    position: absolute;
    width: 100%;
    z-index: 1; }
  .user-wrapper .container {
    padding: 20px;
    background: linear-gradient(to right, #f9df7b, #b57e10); }
    .user-wrapper .container:before {
      margin: 5px;
      background: #fff;
      content: '';
      height: calc(100% - 10px);
      left: 0;
      position: absolute;
      top: 0;
      width: calc(100% - 10px); }
    .user-wrapper .container .user__img {
      margin-top: 10px;
      margin-right: auto;
      margin-left: auto;
      height: 40vh;
      position: relative;
      text-align: center;
      width: 40vh; }
      .user-wrapper .container .user__img:after {
        background: #f9df7b;
        background: linear-gradient(to right, #f9df7b 0%, #b57e10 26%, #f9df7b 83%, #b57e10 99%);
        border-radius: 50%;
        bottom: -5px;
        content: '';
        filter: progid:dximagetransform.microsoft.gradient(startColorstr='#f9df7b', endColorstr='#b57e10', GradientType=1);
        left: -5px;
        position: absolute;
        right: -5px;
        top: -5px;
        z-index: 0; }
      .user-wrapper .container .user__img-image {
        background-size: cover !important;
        border-radius: 50%;
        height: 100%;
        position: relative;
        width: 100%;
        z-index: 1; }
    .user-wrapper .container .user__detail {
      color: #6a420d;
      margin-top: 20px;
      text-align: center;
      text-transform: uppercase; }
    .user-wrapper .container .user__detail,
    .user-wrapper .container .user__img {
      position: relative; }
  .user-wrapper .container_a {
    color: #6a420d;
    text-align: center; }
    .user-wrapper .container_a .detail_a {
      left: 50%;
      position: absolute;
      top: 50%;
      transform: translate(-50%, -50%);
      vertical-align: middle; }

.hero_2 {
  background: url("../../src/images/citrus_bg_1.jpg") center center no-repeat;
  background-size: cover;
  height: 100%;
  position: absolute;
  width: 100%; }
  .hero_2 .container {
