    .hero_2 .container_a .detail_a {
      left: 50%;
      position: absolute;
      top: 50%;
      transform: translate(-50%, -50%);
      vertical-align: middle; }

.user__detail-company {
  font-family: "Montserrat", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: 500; }

.attendees__details {
  margin-bottom: 5px;
